html,
body {
  position: relative;
  width: 100%;
  min-width: 320px;
  background-color: #000000;
  font-family: 'Open Sans', sans-serif;
}
body {
  -ms-overflow-style: scrollbar;
}
body.lock {
  overflow: hidden;
}
main {
  width: 100%;
}
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
}
p {
  margin: 0;
  padding: 0;
}
/* Focus Visible  */
.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
.container {
  width: 100%;
  height: auto;
  margin: 0 auto;
  padding: 0 15px;
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

