/*  header-major ================================= */
.grecaptcha-badge {
  visibility: hidden;
}
#header-major {
  display: block;
  position: fixed;
  top: -300%;
  left: 0;
  width: 100vw;
  z-index: 100000000000;
  transition: all 0.3s ease 0s;
}
#header-major.open-nav {
  height: 100%;
  top: 0;
  background-color: #ffffff;
}
#header-major .header-major__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  position: absolute;
  top: -300%;
  left: 0;
  overflow: auto;
  transition: all 0.3s ease 0s;
  padding: 80px 0;
  z-index: 100000;
}
#header-major .header-major__nav__link {
  text-align: center;
  text-decoration: none;
  margin: 20px auto;
  padding: 5px;
}
#header-major .header-major__nav__link:active {
  background-color: #ffc107;
}
#header-major .header-major__nav__link > h2 {
  margin: 0;
  padding: 0;
  color: #000000;
}
#header-major .header-major__nav.open-nav {
  top: 0;
}
#header-major .header-major__border-burger {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 30px;
  right: 30px;
  width: 37px;
  height: 37px;
  border: 1px solid #ffffff;
  z-index: 10000000000000000000000;
}
#header-major .header-major__border-burger.active {
  border: 1px solid #000000;
}
#header-major .header-major__border-burger__icon {
  display: block;
  position: relative;
  height: 23px;
  width: 27px;
}
#header-major .header-major__border-burger__icon::before,
#header-major .header-major__border-burger__icon::after {
  content: "";
  background-color: #ffffff;
  position: absolute;
  width: 100%;
  height: 3px;
  left: 0;
  transition: all 0.3s ease 0s;
}
#header-major .header-major__border-burger__icon::before {
  top: 0;
}
#header-major .header-major__border-burger__icon::after {
  bottom: 0;
}
#header-major .header-major__border-burger__icon.active::before {
  transform: rotate(-45deg);
  top: 10px;
  background-color: #000000;
}
#header-major .header-major__border-burger__icon.active::after {
  transform: rotate(45deg);
  bottom: 10px;
  background-color: #000000;
}
#header-major .header-major__border-burger__icon__span {
  position: absolute;
  top: 10px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #ffffff;
  transition: all 0.3s ease 0s;
}
#header-major .header-major__border-burger__icon__span.active {
  transform: scale(0);
  background-color: #000000;
}
/* section-header ========================================== */
#section-header {
  width: 100%;
  min-width: 320px;
  height: 100vh;
  min-height: 568px;
  max-height: 1080px;
  background-color: #000000;
  position: relative;
  overflow: hidden;
  background-image: url("../../build/img/background-gif-section-header.gif");
  background-repeat: no-repeat;
  background-size: 590px 590px;
  background-position: center;
}
#section-header .section-header__filter {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}
#section-header video {
  height: 100%;
  position: absolute;
  left: -200px;
  top: 0;
}
#section-header .section-header__wrapper-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70%;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 15px;
  z-index: 100000000;
}
#section-header .section-header__wrapper-content__photo {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  margin: 0;
}
#section-header .section-header__wrapper-content__photo > img {
