button.button-primery,
input.button-primery {
  position: relative;
  background-color: transparent;
  color: #ffffff;
  border: 0;
  width: 135px;
  padding: 10px 10px;
  border: 2px solid #ffffff;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  cursor: pointer;
  transition: all 0.3s, background-color 0s;
}
button.button-primery:disabled,
input.button-primery:disabled {
  color: rgba(255, 255, 255, 0.3);
  border: 2px solid rgba(255, 255, 255, 0.3);
}
button.button-primery:disabled:active,
input.button-primery:disabled:active {
  background-color: transparent;
}
button.button-primery:active,
input.button-primery:active {
  background-color: rgba(255, 193, 7, 0.5);
}
@media screen and (min-width: 992px) {
  button.button-primery,
  input.button-primery {
    width: 150px;
  }
  button.button-primery:focus,
  input.button-primery:focus {
    outline: 1px dotted rgba(255, 193, 7, 0.8);
    outline-offset: 5px;
  }
  button.button-primery:hover,
  input.button-primery:hover {
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
  }
}
